:root {
  --primary_color: #93278f;
  --secondary_color: #3c5d9e;
  --success_color: #0e6d50;
  --danger_color: #ce4f4f;
  --warning_color: #c49653;
  --light_color: #eeebf1;
  --dark_color: #141414;
  --primary_gradient_color: linear-gradient(to right, #662d91, #93278f);
  --secondary_gradient_color: linear-gradient(to right, #6987c4, #4c69a5);
  --success_gradient_color: linear-gradient(to right, #0e6d50, #0a5841);
  --danger_gradient_color: linear-gradient(to right, #ce4f4f, #9c3737);
  --warning_gradient_color: linear-gradient(to right, #c49653, #a37b3e);
  --light_gradient_color: linear-gradient(to right, #ffffff, #eeeeee);
  --dark_gradient_color: linear-gradient(to right, #222222, #1d1b1b);
  --__column_count: 12;
  --__fill_size_threshold: 75;
  --__extra_small_breakpoint: 576px;
  --__small_breakpoint: 768px;
  --__medium_breakpoint: 992px;
  --__large_breakpoint: 1200px;
  --__container_size: var(--__large_breakpoint);
  --__font_normal_size: 12px;
  --__border_size: 12px;
  --__margin_size: 8px;
  --__padding_size: 8px;
  --__loop_count: 9;
  --__size_multiplier: 2;
}

